import {observable,action,decorate,flow} from "mobx"
import {ajax} from "api"

class ListPageState {
	constructor(ROOTSTORE){
		this.ROOTSTORE=ROOTSTORE
		//,
		this.TEMP={
			json:{},
			count:0,
			pagenum:0,
			dataList:[],
		};
		this.JSON={};
	}	
	//
	@observable count=0;
	@observable pagenum=0;
	@observable dataList=[];
	//("PEDDING","COMPLETE","FAIL")
	@observable loadState={
		state:"",
		message:"",
	};
	@action clearInitData(){
		this.count=0;
		this.pagenum=0;
		this.dataList=[];
		this.loadState["state"]=""
		this.loadState["message"]=""			
	}
	@action clearAll(){
		this.TEMP={
			json:{},
			count:0,
			pagenum:0,
			dataList:[],
		}
		this.count=0;
		this.pagenum=0;
		this.dataList=[];
		this.loadState["state"]=""
		this.loadState["message"]=""		
	}
	@action CACHE(){
		this.TEMP["json"]=this.JSON;
		this.TEMP["count"]=this.count;
		this.TEMP["pagenum"]=this.pagenum;
		this.TEMP["dataList"]=this.dataList.slice();
	}
	@action RECOVERY(){
		if(this.TEMP["dataList"].length!==this.dataList.length){
			this.JSON=this.TEMP["json"]
			this.count=this.TEMP["count"]
			this.pagenum=this.TEMP["pagenum"]
			this.dataList=this.TEMP["dataList"]			
		}else{
			return false
		}
	}
	@action PEDDING(text){
		this.loadState["state"]="PEDDING";
		this.loadState["message"]=text
	}
	@action COMPLETE(text){
		this.loadState["state"]="COMPLETE";
		this.loadState["message"]=text
	}
	@action FAIL(text){
		this.loadState["state"]="FAIL";
		this.loadState["message"]=text
	}
	loadInitData=flow(function*(json){
		//
		//020
		//this.pagenum=0;
		this.clearAll()
		this.JSON=json
		const dataOBJ=Object.assign({},this.JSON["data"],{
			pagenum:this.pagenum,
			pagesize:40,				
		})
		try{
			this.PEDDING("loading...")
			//this.ROOTSTORE.Loading.PEDDING("")
			const result=yield ajax.get({
				url:this.JSON["url"],
				data:dataOBJ
			})
			this.count=Number(result["count"])
			this.dataList=result.data
			//pagenum+1
			this.pagenum=this.pagenum+1
			if(this.count==0){
				//this.ROOTSTORE.Loading.COMPLETE("")
					this.COMPLETE("");
			}else{
				this.COMPLETE("")
			}
		}catch(error){
			this.FAIL("")
			throw error;
		}
	})
	loadMoreData=flow(function*(json){
		//
		if(this.dataList.length>=this.count){
			//  this.COMPLETE("")
			return false
		}
		//
		const dataOBJ=Object.assign({},this.JSON["data"],{
			pagenum:this.pagenum,
			pagesize:40,				
		})
		try{
			this.PEDDING("loading...")
			const result=yield ajax.get({
				url:this.JSON["url"],
				data:dataOBJ,
			})
			this.dataList=this.dataList.concat(result["data"])
			//pagenum+1
			this.pagenum=this.pagenum+1	
			this.COMPLETE("")		
		}catch(error){
			this.FAIL("")
			throw error;
		}
	})
	searchData=flow(function*(json){
		//,
		//,
		let condition=true
		for(let key in json){
			//
			if(!json[key]){
				condition=false
				break;
			}
		}
		if(condition){
			//,TEMP,
			this.CACHE()
			this.clearInitData()
			//
			this.pagenum=0;
			this.JSON=json;
			const dataOBJ=Object.assign({},this.JSON["data"],{
				pagenum:this.pagenum,
				pagesize:40,				
			})
			try {
				//this.ROOTSTORE.Loading.PEDDING(",...")
				this.PEDDING("loading...")
				const result=yield ajax.get({
					url:this.JSON["url"],
					data:dataOBJ
				});
				this.count=Number(result["count"])
				this.dataList=result["data"]
				this.pagenum=this.pagenum+1
				if(this.count==0){
					//this.ROOTSTORE.Loading.COMPLETE("")
					this.COMPLETE("")
				}else{
					this.COMPLETE("")
				}
			}catch(error){
				this.FAIL("")
				throw error;
			}
		}else{
			//,TEMP
			this.RECOVERY()
		}
	})
}

export default ListPageState


