import './Points.scss'
import url from "url"
import { ajax } from 'api'
import { withRouter} from "react-router-dom"

@withRouter
class Points extends React.Component{
    render(){
        return(
            <div className="Points">
                    <div className="Points_number">
                                <div className="number">
                                    <p></p>
                                    <p>{this.state.pointsNumber ||'0'}</p>
                                </div>
                                  <div className="care" onClick={this.toLink.bind(this)}></div>
                    </div>
                    <div className="squre"></div>
                    <div className="Points_inform">
                        <span></span>
                        <div className="tab">
                        <table style={{ cellpadding:"0",cellspacing:"0"}}>
                                   <thead>
                                        <tr>
                                            <td>{''}</td>
                                            <td>{''}</td>
                                        </tr>
                                   </thead>
                                   <tbody>
                                       {this.state.dataTable.map((item,index)=>{
                                        return(
                                            <tr key={index}>
                                                <td>{item.CatalogName}</td>
                                                <td>10000{item.CatalogScore}</td>
                                            </tr>
                                        )
                                    })
                                       }
                                       
                                   </tbody>
                                </table>
                                <p></p>
                        </div>
                    </div>
            </div>
        )   
    }
    constructor(props) {
        super(props)
        this.query = url.parse(this.props.location.search, true)["query"]
        this.state={
            pointsNumber:'',
            dataTable:[]
        }
    }
    toLink(){
        this.props.history.push("/Convert?AccountId=" + this.query.AccountId + "&OpenId="+this.query.OpenId);
    }
    async componentDidMount(){
        document.title=""
        try{
            const result = await ajax.get({
                url:"/api/User_Account/User_IntegralDetailListAsync" ,
                data: { openId: this.query['OpenId']}  
            })
           // console.log(result)
            this.setState({ pointsNumber: result.Effectiveintegral})
        }catch(error){
            throw error
        }
        try {
            const result = await ajax.get({
                url: "/api/Wx_Good_Catalog_Score/ListAsync",
                data: { wx_SeetingId: sessionStorage.getItem('SeetingId'),openId: this.query["OpenId"] }
            });
            this.setState({dataTable:result})
        } catch (error) {
            throw error
        }
    }
}
export default Points;


