const ApiHost=hostname

const postFetch=function(postObject){
	const {url,data}=postObject
	let dataString="?"
	for(let key in data){
		switch(key){
			case "host":
				dataString=dataString+key+"="+data[key]+"&"
			break;
			case "pagenum":
				dataString=dataString+key+"="+data[key]+"&"
			break;
			case "pagesize":
				dataString=dataString+key+"="+data[key]+"&"
			break;
			default:
				dataString=dataString+"param."+key+"="+data[key]+"&"
		}
	} 
	dataString=
		dataString.replace(/\&$/ig,"")+
		(function(){
			let checkStr=""
			if(localStorage.getItem("PlantFrom")){
				checkStr=checkStr+"&param.plantFrom="+localStorage.getItem("PlantFrom")
			}
			if(localStorage.getItem("SecretID")){
				checkStr=checkStr+"&param.secretId="+localStorage.getItem("SecretID")
			}
			if(localStorage.getItem("SecretKey")){
				checkStr=checkStr+"&param.secretKey="+localStorage.getItem("SecretKey")
			}
			return checkStr
		})()
	return fetch(ApiHost+url.replace(/^\//ig,"").replace(/\/$/ig,"")+"/"+dataString,{
		method:"POST"
	})
	.catch((error)=>{
		//fetch
		throw "fetch"
	})
	.then((response)=>{
		return response.json()
	})
	.catch((error)=>{
		//
		console.log(`:${url}`);
		throw ","
	})
	.then((result)=>{
		switch(result.Code){
			case "0":
				throw result.Tips
			break;
			case "1":
				return result.Result
			break;
			case "-1":
				throw ""
			break;
			case "-2":
				throw ","
			break;
		}
	})
}

const getFetch=function(getObject){
	const {url,data}=getObject
	let dataString="?"
	for(let key in data){
		switch(key){
			case "host":
				dataString=dataString+key+"="+data[key]+"&"
			break;
			case "pagenum":
				dataString=dataString+key+"="+data[key]+"&"
			break;
			case "pagesize":
				dataString=dataString+key+"="+data[key]+"&"
			break;
			default:
				dataString=dataString+"param."+key+"="+data[key]+"&"
		}
	} 
	dataString=
	dataString.replace(/\&$/ig,"")+
	(function(){
		let checkStr=""
		if(localStorage.getItem("PlantFrom")){
			checkStr=checkStr+"&param.plantFrom="+localStorage.getItem("PlantFrom")
		}
		if(localStorage.getItem("SecretID")){
			checkStr=checkStr+"&param.secretId="+localStorage.getItem("SecretID")
		}
		if(localStorage.getItem("SecretKey")){
			checkStr=checkStr+"&param.secretKey="+localStorage.getItem("SecretKey")
		}
		checkStr=checkStr+`&_=${(new Date()).getTime()}`
		return checkStr
	})()
	return fetch(ApiHost+url.replace(/^\//ig,"").replace(/\/$/ig,"")+"/"+dataString,{
		method:"GET",
	})
	.catch((error)=>{
		//fetch
		throw "fetch"
	})
	.then((response)=>{
		return response.json()
	})
	.catch((error)=>{
		//
		console.log(`:${url}`);
		throw ","
	})
	.then((result)=>{
		switch(result.Code){
			case "0":
				throw result.Tips
			break;
			case "1":
				return result.Result
			break;
			case "-1":
				throw ""
			break;
			case "-2":
				throw ","
			break;
		}
	})
}

export default {
	post:postFetch,
	get:getFetch
}



